import React, {useState} from 'react'
import PropTypes from 'prop-types'


export default function TextForm(props) {
    const handleUpClick = () =>{
        // console.log("Uppercase was clicked" + text);
        let newText = text.toUpperCase();
        setText(newText)
    }
    const handleLowClick = () =>{
        // console.log("Uppercase was clicked" + text);
        let newText = text.toLowerCase();
        setText(newText)
    }
    const handleclcClick = () =>{
        // console.log("Uppercase was clicked" + text);
        let newText = '';
        setText(newText)
    }
    const handlefupClick = () =>{
        let firstchar = text.charAt(0);
        let newText = firstchar.toUpperCase();
        setText(newText + text.slice(1))
    }
    
    const handleOnChange = (event) =>{
        // console.log("On Change")
        setText(event.target.value)
    }
    const [text, setText] = useState('');
    // setText("New Text");
  return (
    <>
    <div className="container" style={{color: props.mode==='dark'?'white':'black'}}>
    <h1>{props.heading}</h1>
    <div className="mb-3">
    <textarea className="form-control" value={text} onChange={handleOnChange} style={{backgroundColor: props.mode==='dark'?'#2b2b2b':'white', color: props.mode==='dark'?'white':'black'}} id="mybox" rows="8"></textarea>
    </div>
    <button className="btn btn-primary mx-1" onClick={handleUpClick}>Convert to Upper case</button>
    <button className="btn btn-primary mx-1" onClick={handleLowClick}>Convert to Lower case</button>
    <button className="btn btn-primary mx-1" onClick={handleclcClick}>Clear all</button>
    <button className="btn btn-primary mx-1" onClick={handlefupClick}> First letter to Upper case</button>
    </div>
    <div className="container my-3" style={{color: props.mode==='dark'?'white':'black'}}>
        <h1>Your text summary</h1>
        <p>{text.split(" ").length} words and {text.length} characters</p>
        <p>{0.008*text.split(" ").length} Miutes to read</p>
        <h2>Preview</h2>
        <p>{text.length>0?text:"Enter something in text box above to preview."}</p>
    </div>
    </>
  )
}

TextForm.propTypes = {
    heading: PropTypes.string,
    barhead: PropTypes.string
}